import React, { useState } from 'react'
import style from 'styled-components'

const Balance = ({ DarkMode, openBalance, OpenCloseBalance, Data }) => {
	var TotalSell = 0
	var Pending = 0
	var Wallet = 0
	var Expenses = 0

	Data.map((data) =>
		data.Amount > 0 ? (TotalSell = TotalSell + data.Amount) : ''
	)
	Data.map((data) =>
		data.Amount < 0 ? (Expenses = Expenses + data.Amount) : ''
	)
	Data.map((data) =>
		data.Status === 'Paid'
			? (Wallet = Wallet + data.Amount)
			: (Pending = Pending + data.Amount)
	)

	return (
		<>
			<BalanceSheet
				className={DarkMode === 1 ? 'addItemLight' : 'addItemDark'}
				style={{
					marginRight: openBalance === 'open' ? '0px' : '-999px'
				}}
			>
				<div className="GoBackB" onClick={() => OpenCloseBalance()}>
					<b>Go Back&nbsp;&nbsp;</b>
					<b>
						<i
							className="fa-solid fa-angle-right fa-lg"
							style={{ color: '#7b5cfa', fontSize: '18px' }}
						></i>
					</b>
				</div>

				<br />
				<br />
				<h1>Balance Sheet</h1>
				<p style={{ color: '#7b5cfa', fontSize: '12px' }}>
					<b>Profit / Loss</b>
				</p>
				<br />
				<br />
				<center>
					<table style={{ color: DarkMode === 1 ? '#000' : '#FFF' }}>
						<tbody>
							<tr>
								<th>Total&nbsp;Sell</th>
								<td>{TotalSell}</td>
							</tr>
							<tr>
								<th>Expenses</th>
								<td style={{ color: '#F00' }}>{Expenses}</td>
							</tr>
							<tr>
								<th>Pending</th>
								<td style={{ color: '#ff9100' }}>{Pending}</td>
							</tr>
							<tr>
								<th>Wallet</th>
								<td style={{ color: '#5F5' }}>{Wallet}</td>
							</tr>
						</tbody>
					</table>
				</center>
			</BalanceSheet>
		</>
	)
}
export default Balance
const BalanceSheet = style.div`
position: fixed;
bottom: 0;
right: 0;
min-height: 100vh;
max-width: 719px;
width: 100%;
border-radius: 0px 10px 0px 10px;
z-index: 0;
top: 62px;
padding: 32px 20px 15px 20px;
transition: 0.5s;

@media (min-width: 768px) {
    top:0;
    width: 522px;
	max-width: 719px;
    padding: 60px 120px 120px 40px;
  }

    .GoBackB{
        display: flex;
        flex-direction: row;
        gap: 5px;
        justify-content: end;
        align-items: center;
        transition: 0.1s;
        font-size: 12px;
        &:hover{
            cursor:pointer;
        }
    }

	table{
		margin: 0px 30px;
		border-collapse: collapse;
		width: 70%;
		tr,td,th{
			border: 1px solid #555;
			padding: 5px 7px;
			text-align: start;
		}
	}

`
