import React, { useState, useEffect } from 'react'
import Header from './components/Header'
import style from 'styled-components'
import { collection, getDocs, onSnapshot } from 'firebase/firestore'
import { db } from './config/fiebase'
import DataTable from './components/DataTable'

function App() {
	const [DarkMode, setDarkMode] = useState(0)
	const [Data, setData] = useState([])
	useEffect(() => {
		const getData = async () => {
			try {
				const dataRef = collection(db, 'Done')
				const dataSnap = await getDocs(dataRef)
				onSnapshot(dataRef, (snapshot) => {
					const dataList = snapshot.docs.map((doc) => {
						return {
							id: doc.id,
							...doc.data()
						}
					})
					let sorted = dataList.sort(function (a, b) {
						return new Date(a.DATE) - new Date(b.DATE)
					})
					setData(sorted)
					return sorted
				})
			} catch (error) {
				console.log(error)
			}
		}
		getData()
	}, [])
	return (
		<>
			<Header DarkMode={DarkMode} setDarkMode={setDarkMode} />
			<MainApp
				className={DarkMode ? 'primaryLightMode' : 'primaryDarkMode'}
			>
				<DataTable Data={Data} DarkMode={DarkMode} setData={setData} />
			</MainApp>
		</>
	)
}
export default App

const MainApp = style.div`
min-height: 100vh;
`
