import React from 'react'
import style from 'styled-components'
import Sun from '../../src/assets/icon-sun.svg';
import Moon from '../../src/assets/icon-moon.svg';
import Logo from '../../src/assets/logo.png';
import Profile from '../../src/assets/profile.png';

const Header = ({ DarkMode, setDarkMode }) => {
	function toggleDarkMode() {
		if (DarkMode === 1) {
			setDarkMode(0)
			document.getElementById('darkmode').style.transform =
				'rotate(360deg)'
			document.getElementById('darkmode2').style.transform =
				'rotate(360deg)'
		} else {
			setDarkMode(1)
			document.getElementById('darkmode').style.transform = 'rotate(0deg)'
			document.getElementById('darkmode2').style.transform =
				'rotate(0deg)'
		}
	}
	return (
		<>
			<Header1
				className={
					DarkMode ? 'secondaryLightMode' : 'secondaryDarkMode'
				}
			>
				<div className="logo">
					<img src={Logo} height="60px" alt='IMG'/>
				</div>
				<Profile1>
					<div
						className="darkmode"
						onClick={toggleDarkMode}
						id="darkmode"
					>
						{DarkMode === 1 ? (
							<img src={Sun} alt='IMG'/>
						) : (
							<img src={Moon} alt='IMG' />
						)}
					</div>
					<div className="profile">
						<img src={Profile} height="60px" alt='IMG'/>
					</div>
				</Profile1>
			</Header1>

			<Header2
				className={
					DarkMode ? 'secondaryLightMode' : 'secondaryDarkMode'
				}
			>
				<div className="logo">
					<img src={Logo} height="60px" alt='IMG'/>
				</div>
				<Profile2>
					<div
						className="darkmode"
						onClick={toggleDarkMode}
						id="darkmode2"
					>
						{DarkMode === 1 ? 
						(<img src={Moon} alt='IMG' />):(<img src={Sun} alt='IMG'/>)
						}
					</div>
					<div className="profile">
						<img src={Profile} alt='IMG' height="60px" />
					</div>
				</Profile2>
			</Header2>
		</>
	)
}
export default Header

const Header1 = style.div`
	position: fixed;
	top:0;
	left: 0;
    display: flex;
	justify-content: space-between;
	height: 60px;
	width: 100%;
	align-items: center;

	.logo{
	position: relative;
	height: 100%;
	}

	@media (max-width: 768px) {
		display: flex;
	  }
	  @media (min-width: 768px) {
		display: none;
	  }
`

const Profile1 = style.div`
display: flex;
flex-direction: row;
gap: 10px;
align-items: center;
	.darkmode{
		transition: 0.5s;
		cursor: pointer;
		transform-origin: position
	}
	.profile img{
		position: relative;
		height: 50px;
		width: 50px;
		border-radius: 50%;
		margin-left: 20px;
		margin-right: 20px;
	}
    
`
const Header2 = style.div`
	position: fixed;
	top:0;
	left: 0;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	height: 100vh;
	width: 60px;
	z-index: 2;
	align-items: center;
	border-radius: 0px 10px 5px 0px;
	@media (max-width: 768px) {
		display: none;
	  }
`

const Profile2 = style.div`
display: flex;
flex-direction: column;
gap: 20px;
align-items: center;

.darkmode{
	transition: 0.5s;
	cursor: pointer;
	transform-origin: position
}
.profile img{
	height: 50px;
	width: 50px;
	border-radius: 50%;
	margin-left: 20px;
	margin-right: 20px;

}
`
