import React, { useState } from 'react'
import style from 'styled-components'
import AddItem from './AddItem'
import Balance from './Balance'
import { db } from '../config/fiebase'
import { deleteDoc, doc } from 'firebase/firestore'
import { ToastContainer, toast } from 'react-toastify'
import 'react-toastify/dist/ReactToastify.css'
import moment from 'moment'

const DataTable = ({ Data, DarkMode, setData }) => {
	const [open, setOpenAdd] = useState('close')
	const [Update, setUpdate] = useState(false)
	const [UpInvoice, setUpInvoice] = useState({})
	const [Searc, setSearc] = useState('')
	//const [Filtered, setFiltered] = useState('')
	const [openBalance, setopenBalance] = useState('close')
	//const [modalIsOpen, setModalIsOpen] = useState(false)
	//const [Password, setPassword] = useState('')

	var TotalSell = 0
	var Pending = 0
	var Wallet = 0

	function OpenClose() {
		if (open === 'open') {
			setOpenAdd('close')
		} else {
			setOpenAdd('open')
			setUpdate(false)
			setUpInvoice({})
		}
	}
	function OpenCloseBalance() {
		if (openBalance === 'open') {
			setopenBalance('close')
		} else {
			setopenBalance('open')
		}
	}

	const deleteCard = async (id,name) => {
		try {
			var delPass = prompt('Please Enter Password')
			if ((name==='Test' && delPass === 'Test')|| delPass==='9999') {
				toast.error('Invoice Deleted Successfully')
				await deleteDoc(doc(db, 'Done', id))
			} else {
				toast.error('Wrong Password')
			}
		} catch (error) {
			console.log(error)
		}
	}
	const addedItemMessage = () => {
		toast.success('Invoice Added Successfully')
	}
	const UpdateItemMessage = () => {
		toast.success('Invoice Updated Successfully')
	}

	function Search() {
		return Data.filter((dat) => dat.NAME.includes(Searc))
	}

	function UpdateInvoice(Data) {
		var UpdatePass = prompt('Please Enter Password')
		if ((Data.NAME==='Test' && UpdatePass === 'Test') || UpdatePass==='9999'){
			OpenClose()
			setUpdate(true)
			setUpInvoice(Data)
		} else {
			toast.error('Wrong Password')
		}
	}

	return (
		<Section>
			<ToastContainer />
			<Balance
				DarkMode={DarkMode}
				openBalance={openBalance}
				OpenCloseBalance={OpenCloseBalance}
				Data={Data}
			/>
			<AddItem
				DarkMode={DarkMode}
				open={open}
				UpInvoice={UpInvoice}
				setOpenAdd={setOpenAdd}
				OpenClose={OpenClose}
				isUpdate={Update}
				setUpdate={setUpdate}
				addedItemMessage={addedItemMessage}
				UpdateItemMessage={UpdateItemMessage}
			/>
			<Head>
				<h1>Invoices</h1>
				<div className="filterButton">
					<select
						className={
							DarkMode === 1
								? 'tableLightMode inputFilter'
								: 'tableDarkMode inputFilter'
						}
					>
						<option value="">All</option>
						<option value="Paid">Paid</option>
						<option value="Pending">Pending</option>
					</select>
					<div className="HeadButton">
						<Button onClick={OpenClose}>
							<div>+</div>New Bill
						</Button>
					</div>
				</div>
			</Head>
			<Head2>
				<input
					type="search"
					placeholder="Search by client name"
					className={
						DarkMode === 1
							? 'tableLightMode inputSearch'
							: 'tableDarkMode inputSearch'
					}
					onChange={(e) => setSearc(e.target.value)}
				/>
				<Button3 onClick={OpenCloseBalance}>Balance</Button3>
			</Head2>
			<Table>
				{Search().map((data, i) => {
					TotalSell = data.Amount + TotalSell
					data.Status === 'Paid'? (Wallet = Wallet + data.Amount): (Pending = Pending + data.Amount)
					return (
						<div key={i} className="tr">
							<div
								className={
									DarkMode === 1
										? 'tableLightMode'
										: 'tableDarkMode'
								}
							>
								<p>
									<b>
										{moment(data.DATE).format(
											'DD MMM YYYY'
										)}
									</b>
								</p>
								<p style={{ fontWeight: '300' }}>{data.NAME}</p>
								<p>{data.Qty}</p>
								<p>{data.Price}</p>
								<p
									style={{
										color:
											data.Amount < 0 ? 'red' : DarkMode===1?'#008000':'#00F000',
										fontWeight: DarkMode===1?'600':'500'
									}}
								>
									{data.Amount}
								</p>
								<p
									className="Status"
									style={{
										background:
											data.Status === 'Paid'
												? '#0080001F'
												: '#ff91000F',
										color:
											data.Status === 'Paid'
												? '#008000'
												: '#ff9100'
									}}
								>
									<span
										style={{
											background:
												data.Status === 'Paid'
													? '#008000'
													: '#ff9100'
										}}
									></span>
									{data.Status}
								</p>
								<i
									className="fas fa-edit fa-lg"
									style={{ color: '#7b5cfa' }}
									onClick={() => {
										UpdateInvoice(data)
									}}
								></i>
								<i
									onClick={() => deleteCard(data.id, data.NAME)}
									className="fas fa-trash fa-lg"
									style={{ color: '#F00' }}
								></i>
							</div>
						</div>
					)
				})}
			</Table>
		</Section>
	)
}
export default DataTable

const Section = style.div`
width: 100%;
max-width: 730px;
margin: 0px auto;
padding-bottom: 48px;

.inputSearch{
	padding: 10px;
    outline: none;
    border: 0.1px solid transparent;
    font-weight: 500;
	width: 250px;
    border-radius: 6px;
	text-transform: capitalize;

    &:focus{
        border: 0.1px solid #7b5cfa;
    }
	@media (min-width: 768px) {
		width:450px;
	}
}
.inputFilter{
	display:none;
    padding: 2px 10px;
    outline: none;
    border: 0.1px solid transparent;
    font-weight: 500;
	height:30px;
    width: 100px;
    border-radius: 6px;
	text-transform: capitalize;

    &:focus{
        border: 0.1px solid #7b5cfa;
    }
`
const Head = style.div`
    display: flex;
    padding: 72px 0 50px 0;
    align-items: center;
    justify-content: space-between;
    margin: 0px 10px;

	.filterButton{
		display: flex;
		flex-direction: row;
		gap: 10px;
		align-items: center;
		justify-content:center;
	}
`
const Head2 = style.div`
	display: grid;
	grid-template-columns: auto auto;
	justify-content: center;
	align-items: center;
	margin: 20px 15px 30px 15px;
	gap: 25px;
`

const Table = style.div`
    margin: 0px 15px;
    .tr div{
        display: grid;;
		grid-template-rows: 1fr 1fr;
		grid-template-columns: 100px 150px 50px;
		border: 0.1px solid transparent;
        align-items: center;
		justify-content: space-between;
        border-radius: 5px;
        padding: 10px;
        margin: 15px;
		transition: border 350ms ease-in-out;
		font-size: 14px;
		p:nth-child(3){display: none;}
		p:nth-child(4){display: none;}

        &:hover{
            border: 0.1px solid #7b5cfa;
            cursor:pointer;
        }
		@media (min-width: 768px) {
			width: 730px;
			grid-template-columns: 130px 140px 70px 70px 70px 130px 60px 60px;
			grid-template-rows: unset;
			grid-template-area:unset;
			p:nth-child(3){display: block;}
		p:nth-child(4){display: block;}
		  }
    }
	.tr{
		.Status{
			width:80px;
			padding: 7px 2px;
			text-align: center;
			font-weight: 700;
			border-radius: 6px;
			font-size: 12px;
			@media (min-width: 768px) {
				width:100px;
			}

			span{
				display:inline-block;
				margin-right:8px;
				height: 10px;
				width: 10px;
				border-radius: 50%;
			}
		}
	}
    p{
        padding: 10px 00px;
    }
    
`
const Button3 = style.button`
    display: flex;
    align-items: center;
    border: none;
    border-radius: 6px;
    padding: 10px 10px 10px 10px;
    background: green;
    font-size: 14px;
    font-weight: 700;
    color: #FFF;
    cursor: pointer;
	outline: none;
	`
const Button = style.button`
    display: flex;
    align-items: center;
    justify-content: space-between;
    border: none;
    border-radius: 24px;
    padding: 10px 10px 10px 10px;
    background: #7b5cfa;
    font-size: 14px;
    font-weight: 700;
    color: #FFF;
    cursor: pointer;
	outline: none;

    div{
		display: flex;
		justify-content: center;
		align-items: center;
        width: 32px;
        height: 32px;
        border-radius: 50px;
        background: #FFF;
        color: #7b5cfa;
		font-size: 24px;
        margin-right: 5px;
    }

`
